import React, { useContext } from 'react'
import styled from 'styled-components'
import { SwapPoolTabs } from '../../components/NavigationTabs'
import './style.css'
import title1 from '../../assets/images/nft/title1.png'
import title2 from '../../assets/images/nft/title2.png'
import title3 from '../../assets/images/nft/title3.png'
import { ThemeContext } from 'styled-components'
import { Text } from 'rebass'
import { ButtonConfirmed } from '../../components/Button'
import { useTranslation } from 'react-i18next'

export const NftWrapper = styled.div`
  position: relative;
  max-width: 420px;
  width: 100%;
`

/**
 * The styled container element that wraps the content of most pages and the tabs.
 */
export default function Nft() {
  const theme = useContext(ThemeContext)
  const { t } = useTranslation()
  const inputBox = {
    width: '100%',
    height: '60px'
  }
  const inputText = {
    width: '100%',
    height: '100%',
    background: theme.text6,
    borderRadius: '15px',
    color: theme.text1,
    paddingLeft: '15px',
    border: 'none',
    fontSize: '20px'
  }
  const btnStyle = {
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    marginTop: '20px'
  }
  // const saveStyle = {
  //   width: '100%',
  //   marginTop: '100px'
  // }
  return (
        <NftWrapper>
            <SwapPoolTabs active={'NFT'} />
            <div className='nftBox'>
              <div className='titleBox'>
                <img className='img1' src={title1} alt="" />
                <img className='img2' src={title2} alt="" />
                <img className='img3' src={title3} alt="" />
              </div>
              <div className='nftText' color={theme.text6}>
                NFT
              </div>
              <div style={inputBox}>
                <input style={inputText} placeholder={t('inputAddress')} type="text" />
              </div>
              <div style={btnStyle}>
                <ButtonConfirmed style={{height: '2.2rem',width:'10rem'}}>
                  <Text fontWeight={500} fontSize={20}>
                    {t('search')}
                  </Text>
                </ButtonConfirmed>
              </div>
              {/* <div style={saveStyle}>
                <ButtonConfirmed style={{height: '2.5rem',width:'10rem'}}>
                  <Text fontWeight={500} fontSize={20}>
                    {t('collection')}
                  </Text>
                </ButtonConfirmed>
              </div> */}
            </div>
        </NftWrapper>
  )
}
