import ajax from './ajax'
/*
* ajax 从 ajax.js 引入 需要
* 第一个 参数 是 url 必填
* 第二个 参数 是 params对象 默认 {} 非必填
* 第三个 参数 是 GET、POST 请求方式， 默认 POST， 非必填
*/
// 后台地址
const BASE_URL:string = `https://ip.opencc.xyz/ip/v1/helper/bannedIP`

// 登陆接口
export const ReqLogin = () => ajax(`${BASE_URL}`, {}, 'GET') // POST 是默认值，可以不写
