
import React, { useContext } from 'react'
import styled from 'styled-components'
import { SwapPoolTabs } from '../../components/NavigationTabs'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { ButtonConfirmed } from '../../components/Button'
import { useTranslation } from 'react-i18next'
import { ethers } from 'ethers'
import { useIsDarkMode } from '../../state/user/hooks'
import { useMultipleContractSingleData } from '../../state/multicall/hooks'
import ERC20_INTERFACE from '../../constants/abis/erc20'

import { useActiveWeb3React } from '../../hooks'
import sortImg from '../../assets/images/sort.png'
import pgwIcon from '../../assets/images/logo.png'
import usdtIcon from '../../assets/images/USDT.png'
import kLine from '../../assets/images/kLine.png'
import kLine2 from '../../assets/images/kLine2.png'

export const HomeWrapper = styled.div`
  position: relative;
  maxWidth: 420px;
  width: 100%
`

/**
 * The styled container element that wraps the content of most pages and the tabs.
 */
export default function Home() {
  const { t } = useTranslation()
  const theme = useContext(ThemeContext)
  const darkMode = useIsDarkMode()
  const topCenterBox = {
    background: theme.bg1,
    boxShadow: `0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
      0px 24px 32px rgba(0, 0, 0, 0.01)`,
    borderRadius: '30px',
    padding: '1rem'
  }
  const homeBox = {
    display: 'flex',
    justifyContent: 'space-between',
    padding: '0 20px',
    alignItems: 'center'
  }
  const sortIcon = {
    width: '20px',
    height: '20px'
  }
  const coinBlanceBox = {
    marginTop: '20px'
  }
  const coinTitle = {
    fontSize: '15px',
    fontWeight: 600,
    color: theme.text1,
    marginTop: '20px'
  }
  const coinSmallBox = {
    display: 'flex',
    justifyContent: 'space-between',
  }
  const lists = {
    width: '45%',
    padding: '5px 0',
    lineHeight: '25px',
    textAlign: 'center' as 'center',
    background: theme.bg6,
    borderRadius: '50px',
    marginTop: '10px'
  }
  const coinPrice = {
    display: 'flex',
    marginTop: '15px',
    padding: '12px 20px',
    background: theme.bg6,
    color: theme.text1,
    borderRadius: '50px',
    fontSize: '15px',
    width: '100%'
  }
  const statusBox = {
    display: 'flex',
    justifyContent: 'space-between',
  }
  const poolStatusBox = {
    width: '45%',
    padding: '10px 0',
    textAlign: 'center' as 'center',
    background: theme.bg6,
    borderRadius: '20px',
    marginTop: '10px'
  }
  const poolStatusBoxText = {
    color: theme.text1,
    fontSize: '14px',
    lineHeight: '20px',
    marginBottom: '5px'
  }
  const numColor = {
    color: 'rgb(170,165,163)'
  }
  const coinStyle = {
    width: '25px',
    height: '25px',
    marginRight: '5px'
  }
  const saveStyle = {
    width: '100%',
    marginTop: '20px'
  }
  const kLineImg = {
    width: '100%',
    height: '150px',
    borderRadius: '20px',
    marginTop: '10px'
  }
  const { account } = useActiveWeb3React()
  const userAddress = JSON.parse(JSON.stringify(account)).split(' ')
  const balances1 = useMultipleContractSingleData(['0x55d398326f99059fF775485246999027B3197955'], ERC20_INTERFACE, 'balanceOf', userAddress)
  const balances2 = useMultipleContractSingleData(['0x0Bc5772C76d274f2d387592b832dd3daE2AeEdF5'], ERC20_INTERFACE, 'balanceOf', userAddress)
  const getGnaPoolNum = useMultipleContractSingleData(['0x0Bc5772C76d274f2d387592b832dd3daE2AeEdF5'], ERC20_INTERFACE, 'balanceOf', ['0x4f1C28a0286F64E76D7DA9F23a1f93a7f382CE2C'])
  const getUSDTPoolNum = useMultipleContractSingleData(['0x55d398326f99059fF775485246999027B3197955'], ERC20_INTERFACE, 'balanceOf', ['0x4f1C28a0286F64E76D7DA9F23a1f93a7f382CE2C'])
  
  const value1 = balances1?.[0]?.result?.[0]
  const value2 = balances2?.[0]?.result?.[0]
  const value3 = getGnaPoolNum?.[0]?.result?.[0]
  const value4 = getUSDTPoolNum?.[0]?.result?.[0]
  
  const usdtNum = value1 ? JSON.parse(ethers.utils.formatUnits(value1.toString())) : 0
  const gnaNum = value2 ? JSON.parse(ethers.utils.formatUnits(value2.toString())) : 0
  const gnaPoolNum = value3 ? JSON.parse(ethers.utils.formatUnits(value3.toString())) : 0
  const usdtPoolNum = value4 ? JSON.parse(ethers.utils.formatUnits(value4.toString())) : 0

  return (
        <HomeWrapper>
            <div style={topCenterBox}>
              <SwapPoolTabs active={'home'} />
              <div style={homeBox}>
                <ButtonConfirmed style={{height: '1.5rem',width:'7rem'}}>
                  <img style={coinStyle} src={usdtIcon} alt="" />
                  <Text fontWeight={500} fontSize={20}>
                    USDT
                  </Text>
                </ButtonConfirmed>
                <img src={sortImg} style={sortIcon} alt="" />
                <ButtonConfirmed style={{height: '1.5rem',width:'7rem'}}>
                  <img style={coinStyle} src={pgwIcon} alt="" />
                  <Text fontWeight={500} fontSize={20}>
                    PGD
                  </Text>
                </ButtonConfirmed>
              </div>
              <div style={coinBlanceBox}>
                <div style={coinTitle}>
                  {t('youAccountNum')}：
                </div>
                <div style={coinSmallBox}>
                  <div style={lists}>
                    <Text fontWeight={500} fontSize={18}>
                      USDT
                    </Text>
                    <Text fontWeight={500} fontSize={16} style={numColor}>
                      {usdtNum.toFixed(5).slice(0, -1)}
                    </Text>
                  </div>
                  <div style={lists}>
                    <Text fontWeight={500} fontSize={18}>
                      PGD
                    </Text>
                    <Text fontWeight={500} fontSize={16} style={numColor}>
                      {gnaNum.toFixed(5).slice(0, -1)}
                    </Text>
                  </div>
                </div>
                <div style={coinTitle}>
                  {t('youNumAsset')}：1PGD = 1USDT
                </div>
                <div style={coinPrice}>
                  USDT ≈ <Text style={numColor} paddingLeft='5px'> {usdtNum.toFixed(5).slice(0, -1)}</Text>
                </div>
                <div style={coinPrice}>
                  PGD ≈ <Text style={numColor} paddingLeft='5px'> {gnaNum.toFixed(5).slice(0, -1)}</Text>
                </div>
                <div style={coinTitle}>
                  {t('nowPoolStatus')}
                </div>
                <div style={statusBox}>
                  <div style={poolStatusBox}>
                    <div style={poolStatusBoxText}>
                      <div>USDT</div>
                      <div style={numColor}>{usdtPoolNum.toFixed(5).slice(0, -1)}</div>
                    </div>
                    {/* <div style={poolStatusBoxText}>
                      <div>{t('serviceCharge')}</div>
                      <div style={numColor}>0.00</div>
                    </div> */}
                    {/* <div style={poolStatusBoxText}>
                      <div>{t('turnover')}（24h）</div>
                      <div style={numColor}>0.00</div>
                    </div> */}
                    {/* <div style={poolStatusBoxText}>
                      <div>{t('serviceCharge')}（24h）</div>
                      <div style={numColor}>0.00</div>
                    </div> */}
                  </div>
                  <div style={poolStatusBox}>
                    <div style={poolStatusBoxText}>
                      <div>PGD</div>
                      <div style={numColor}>{gnaPoolNum.toFixed(5).slice(0, -1)}</div>
                    </div>
                    {/* <div style={poolStatusBoxText}>
                      <div>{t('serviceCharge')}</div>
                      <div style={numColor}>0.00</div>
                    </div> */}
                    {/* <div style={poolStatusBoxText}>
                      <div>{t('turnover')}（24h）</div>
                      <div style={numColor}>0.00</div>
                    </div> */}
                    {/* <div style={poolStatusBoxText}>
                      <div>{t('serviceCharge')}</div>
                      <div style={numColor}>0.00</div>
                    </div> */}
                  </div>
                </div>
              </div>
            </div>
            <div style={saveStyle}>
              <ButtonConfirmed style={{height: '1rem',width:'6rem'}}>
                <Text fontWeight={500} fontSize={18}>
                  K线图
                </Text>
              </ButtonConfirmed>
              <img style={kLineImg} src={darkMode?kLine:kLine2} alt="" />
            </div>
        </HomeWrapper>
        
  )
}
