import React from 'react'
import styled from 'styled-components'
import { darken } from 'polished'
import { useTranslation } from 'react-i18next'
import { NavLink, Link as HistoryLink } from 'react-router-dom'
import homeImg from '../../assets/images/navImg/homeActve.png'
import changeImg from '../../assets/images/navImg/change1.png'
import poolImg from '../../assets/images/navImg/pool.png'
import nftImg from '../../assets/images/navImg/nft.png'
import myImg from '../../assets/images/navImg/my.png'

import { ArrowLeft } from 'react-feather'
import { RowBetween } from '../Row'
import QuestionHelper from '../QuestionHelper'

const Tabs = styled.div`
  ${({ theme }) => theme.flexRowNoWrap}
  align-items: center;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px 20px 20px 20px;
  background: ${({ theme }) => theme.bg1};
  z-index: 9
`

const activeClassName = 'ACTIVE'

const StyledNavLink = styled(NavLink).attrs({
  activeClassName
})`
  height: 3rem;
  border-radius: 3rem;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  color: ${({ theme }) => theme.text3};
  font-size: 16px;
  div{
    display: flex;
    justify-content: center;
    img{
      width: 35px;
      height: 35px;
      display: block;
      margin-bottom: 5px;
    }
  }
  
  &.${activeClassName} {
    border-radius: 12px;
    font-weight: 600;
    color: ${({ theme }) => theme.text1};
    font-size: 21px;
    div{
      display: flex;
      justify-content: center;
      img{
        margin-bottom: 10px;
        margin-top: -30px;
        width: 50px;
        height: 50px;
        display: block;
      }
    }
    
  }

  :hover,
  :focus {
    color: ${({ theme }) => darken(0.1, theme.text1)};
  }
`

const ActiveText = styled.div`
  font-weight: 500;
  font-size: 20px;
`

const StyledArrowLeft = styled(ArrowLeft)`
  color: ${({ theme }) => theme.text1};
`

export function SwapPoolTabs({ active }: { active: 'home' | 'swap' | 'pool' | 'NFT' | 'myPage' }) {
  const { t } = useTranslation()
  return (
    <Tabs>
      <StyledNavLink id={`home-nav-link`} to={'/home'} isActive={() => active === 'home'}>
        <div>
          <img src={homeImg} alt="" />
        </div>
        {t('home')}
      </StyledNavLink>
      <StyledNavLink id={`swap-nav-link`} to={'/swap'} isActive={() => active === 'swap'}>
        <div>
          <img src={changeImg} alt="" />
        </div>
        {t('swap')}
      </StyledNavLink>
      <StyledNavLink id={`pool-nav-link`} to={'/pool'} isActive={() => active === 'pool'}>
        <div>
          <img src={poolImg} alt="" />
        </div>
        {t('pool')}
      </StyledNavLink>
      <StyledNavLink id={`nft-nav-link`} to={'/NFT'} isActive={() => active === 'NFT'}>
        <div>
          <img src={nftImg} alt="" />
        </div>
        NFT
      </StyledNavLink>
      <StyledNavLink id={`my-nav-link`} to={'/myPage'} isActive={() => active === 'myPage'}>
        <div>
          <img src={myImg} alt="" />
        </div>
        {t('My')}
      </StyledNavLink>
    </Tabs>
  )
}

export function FindPoolTabs() {
  const { t } = useTranslation()
  return (
    <Tabs>
      <RowBetween style={{ padding: '1rem' }}>
        <HistoryLink to="/pool">
          <StyledArrowLeft />
        </HistoryLink>
        <ActiveText>{t('ImportPool')}</ActiveText>
        <QuestionHelper text={t('QuestionHelperText3')} />
      </RowBetween>
    </Tabs>
  )
}

export function AddRemoveTabs({ adding }: { adding: boolean }) {
  const { t } = useTranslation()
  return (
    <Tabs>
      <RowBetween style={{ padding: '1rem' }}>
        <HistoryLink to="/pool">
          <StyledArrowLeft />
        </HistoryLink>
        <ActiveText>{adding ? t('Add') : t('Remove')}{t('Liquidity')}</ActiveText>
        <QuestionHelper
          text={
            adding
              ? t('QuestionHelperText1')
              : t('QuestionHelperText2')
          }
        />
      </RowBetween>
    </Tabs>
  )
}
