import React, { useContext } from 'react'
import styled from 'styled-components'
import { SwapPoolTabs } from '../../components/NavigationTabs'
import { ThemeContext } from 'styled-components'
import { Text } from 'rebass'
import { useTranslation } from 'react-i18next'
import { useDarkModeManager } from '../../state/user/hooks'
import Toggle from '../../components/Toggle'
import setImg1 from '../../assets/images/navImg/setImg1.png'
import setImg2 from '../../assets/images/navImg/setImg2.png'
// import setImg3 from '../../assets/images/navImg/setImg3.png'
import QsLang from '../../components/QsLang'

// import setImg4 from '../../assets/images/navImg/setImg4.png'
// import setImg41 from '../../assets/images/navImg/setImg4-1.png'
import logoImg from '../../assets/images/logo.png'


export const HomeWrapper = styled.div`
  position: relative;
  maxWidth: 420px;
  width: 100%
`

/**
 * The styled container element that wraps the content of most pages and the tabs.
 */
export default function Home() {
  
  const { t } = useTranslation()
  const theme = useContext(ThemeContext)
  const [darkMode, toggleDarkMode] = useDarkModeManager()
  const topCenterBox = {
    background: theme.bg7,
    boxShadow: `0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
      0px 24px 32px rgba(0, 0, 0, 0.01)`,
    borderRadius: '30px',
    padding: '2rem 1rem',
    marginTop: '50px'
  }
  const coinPrice = {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: '15px',
    padding: '20px 30px',
    background: theme.bg6,
    color: theme.text1,
    borderRadius: '20px',
    fontSize: '15px',
    width: '100%'
  }
  const coinPrice1 = {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    margin: '15px 0',
    padding: '20px 15px 20px 30px',
    background: theme.bg6,
    color: theme.text1,
    borderRadius: '20px',
    fontSize: '15px',
    width: '100%'
  }
  const coinStyle = {
    width: '30px',
    height: '30px'
  }
  const bottomBox = {
    marginTop: '30px',
    width: '100%'
  }
  const imgBox = {
    display: 'flex',
    justifyContent: 'center'
  }
  const logoStyle = {
    width: '80px',
    height: '100px'
  }
  const bottomText = {
    display: 'flex',
    justifyContent: 'center'
  }
  
  return (
        <HomeWrapper>
            <div style={topCenterBox}>
                <SwapPoolTabs active={'myPage'} />
                <div style={coinPrice}>
                    <Text fontWeight={500} fontSize={22}>
                      {t('myNode')}
                    </Text>
                    <img style={coinStyle} src={setImg1} alt="" />
                </div>
                <div style={coinPrice}>
                    <Text fontWeight={500} fontSize={22}>
                      {t('theCode')}
                    </Text>
                    <img style={coinStyle} src={setImg2} alt="" />
                </div>
                <div style={coinPrice}>
                    <Text fontWeight={500} fontSize={22}>
                      {t('languageSwitch')}
                    </Text>
                    <QsLang/>
                </div>
                <div style={coinPrice1}>
                    <Text fontWeight={500} fontSize={22}>
                      {t('modeSwitch')}
                    </Text>
                    <Toggle isActive={darkMode} toggle={toggleDarkMode} />
                </div>
            </div>
            <div style={bottomBox}>
                <div style={imgBox}>
                    <img style={logoStyle} src={logoImg} alt="" />
                </div>
                <div style={bottomText}>
                    <Text fontWeight={500} fontSize={30}>
                      PGDSWAP
                    </Text>
                </div>
                <div style={bottomText}>
                    <Text fontWeight={500} fontSize={15}>
                        v 1.0.1
                    </Text>
                </div>
            </div>
        </HomeWrapper>
        
  )
}
