import React from 'react'
import i18next from 'i18next'
// import styled from 'styled-components'
// import useQsLang from '../../hooks/useQsLang'

export interface ToggleProps {
  isActive: boolean,
  toggle1: () => void
}

export default function QsLang() {
  return (
    <div onClick={()=>i18next.changeLanguage(i18next.language==='en'?'zh-cn':'en')}>
      {i18next.language==='en'?'English':'中文简体'}
    </div>
  )
}

